import { OnInit, HostBinding, Directive } from '@angular/core';
import { AvlDialogComponent } from './avl-dialog.component';

@Directive()
export abstract class AvlBaseDialogSection implements OnInit {

  constructor(protected dialog: AvlDialogComponent) { }

  @HostBinding('class.complex')
  isComplex: boolean = false;

  /** @hidden */
  closeDialog() {
    this.dialog.closeDialog();
  }

  ngOnInit() {
    this.isComplex = this.dialog.complex;
  }

}
