import {
  Component,
  ElementRef,
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ViewEncapsulation,
  ContentChild,
  TemplateRef,
  HostBinding,
  HostListener,
  OnInit
} from '@angular/core';
import { ConfigurableFocusTrapFactory } from '@angular/cdk/a11y';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'avl-dialog',
  templateUrl: './avl-dialog.component.html',
  styleUrls: ['./avl-dialog.component.css'],
  encapsulation: ViewEncapsulation.Emulated
})
export class AvlDialogComponent implements OnInit {
  private _open = false;
  private _dirty: boolean = false;
  private _warningOnClose = false;
  private _warningHeaderText = '';
  private _warningDialogText = '';
  public openWarning = false;

  /**
   * Dialog content
   */
  @ContentChild(TemplateRef) content: TemplateRef<any>;

  /** @hidden */
  @ViewChild('element', { static: true }) element: ElementRef;

  /** @hidden */
  @ViewChild('optionForm', { read: NgForm }) optionForm: any;

  /**
   * If true, the dialog can be closed by clicking on scrim
   */
  @Input() closeOnScrimClick: boolean = true;

  /**
   * If true, new dialog can open inside a dialog
   */
  @Input() complex: boolean = false;

  /**
   * Sets the text for warning dialog header.
   */
  @Input()
  get warningHeaderText(): string {
    return this._warningHeaderText;
  }
  set warningHeaderText(value: string) {
    if (this._warningHeaderText !== value) {
      this._warningHeaderText = value;
    }
  }

  /**
   * Sets the main warning dialog text.
   */
  @Input()
  get warningDialogText(): string {
    return this._warningDialogText;
  }
  set warningDialogText(value: string) {
    if (this._warningDialogText !== value) {
      this._warningDialogText = value;
    }
  }

  /**
   * When checked, dialog opens in fullscreen mode
   */
  @HostBinding('class.fullscreen')
  @Input()
  fullscreen = false;

  /**
   * Can be set to open or close the dialog.
   * Returns the current state of the dialog.
   */
  @Input()
  get open(): boolean {
    return this._open;
  }
  set open(value: boolean) {
    if (this._open !== value) {
      this._open = !!value;
      this.openChange.emit(this._open);
      this.focusTrap.create(this.element.nativeElement).focusFirstTabbableElementWhenReady();
    }
  }

  /**
   * Indicates if the form has been touched.
   * Returns the current value of the attribute.
   */
  @Input()
  get dirty(): boolean {
    return !!this._dirty;
  }
  set dirty(value: boolean) {
    if (this._dirty !== value) {
      this._dirty = !!value;
      this.focusTrap.create(this.element.nativeElement).focusFirstTabbableElementWhenReady();
    }
  }

  /**
   * Can be set to indicate if the warning dialog should be opened.
   * Returns the current value of the attribute.
   */
  @Input()
  get warningOnClose(): boolean {
    return !!this._warningOnClose;
  }
  set warningOnClose(value: boolean) {
    if (this._warningOnClose !== value) {
      this._warningOnClose = !!value;
      this.focusTrap.create(this.element.nativeElement).focusFirstTabbableElementWhenReady();
    }
  }

  @Output() openChange = new EventEmitter<boolean>();

  /** @hidden */
  close(event: MouseEvent) {
    const target = event.target as HTMLElement;
    if (this.closeOnScrimClick && target.classList.contains('scrim')) {
      if (this.warningOnClose) {
        if (this.openWarning === false) {
          this.openWarning = true;
        }
      } else {
        if (this.dirty === true) {
          this.openWarning = true;
        } else {
          this.open = false;
        }
      }
    }
  }

  /**
   * Opens a dialog.
   */
  openDialog() {
    this.open = true;
  }

  /**
   * Closes a dialog.
   */
  closeDialog() {
    if (this.warningOnClose || this.dirty) {
      this.openWarning = !this.openWarning;
    } else {
      this.open = false;
    }
  }

  /**
   * Closes all dialogs.
   */
  closeAllDialogs() {
    this.openWarning = false;
    this.open = false;
    this.dirty = false;
  }

  /**
   * Opens a warning dialog.
   */
  openWarningDialog() {
    this.openWarning = true;
  }

  /**
   * Closes a warning dialog.
   */
  closeWarningDialog() {
    this.openWarning = false;
  }

  /** @hidden */
  @HostListener('keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (event.key === 'Esc' || event.key === 'Escape') {
        this.closeDialog();
    }
  }

  /** @hidden */
  @HostBinding('class.opened')
  get openedClosed() {
    return this._open;
  }

  constructor(private focusTrap: ConfigurableFocusTrapFactory) {}

  /** @hidden */
  ngOnInit() {
    if (this.complex != null && this.complex !== false) {
      this.complex = true;
    }
  }

  /**
   *
   * ### Styling
   * The following custom properties are available for styling:
   *
   * Custom property                     | Description                       | Default
   * :------------------------------------------|:--------------------------------------|:----------------------
   * `--avl-dialog-background-color`     | Background color of dialog        | `--avl-background-color`
   * `--avl-dialog-text-color`           | Color of text in content          | `--avl-emphasis-medium-color`
   * `--avl-dialog-header-title-color`   | Color of text in header           | `--avl-foreground-color`
   * `--avl-dialog-close-icon-color`     | Color of close icon in header     | `--avl-icon-default-color`
   * `--avl-dialog-width`                | Dialog width                      | `280 px`
   * `--avl-dialog-height`               | Dialog height                     | `100% - 96px`
   * `--avl-dialog-separator-color`      | Color of separator of dialog parts| `--avl-divider-color`
   * `--avl-dialog-close-icon-ink-pressed-color`| Color of pressed close icon in header | `--avl-ink-default-pressed-color`
   * `--avl-dialog-close-icon-ink-focus-color`  | Color of focused close icon in header | `--avl-ink-default-focus-color`
   * `--avl-dialog-close-icon-ink-hover-color`  | Color of hovered close icon in header | `--avl-ink-default-hover-color`
   *
   */
  _styling: any;
}
