import { Component, ViewChild, ElementRef, AfterViewInit, HostBinding } from '@angular/core';
import { AvlDialogComponent } from '../avl-dialog.component';
import { AvlBaseDialogSection } from '../avl-dialog-section.base';

import '@avl-controls/core/dist/avl-icon-button/avl-icon-button';
import '@avl-controls/core/dist/avl-tooltip/avl-tooltip';

@Component({
  selector: 'avl-dialog-header',
  templateUrl: './avl-dialog-header.component.html',
  styleUrls: ['./avl-dialog-header.component.css']
})
export class AvlDialogHeaderComponent extends AvlBaseDialogSection implements AfterViewInit {
  @HostBinding('class.extended')
  extended = false;

  @ViewChild('extendedSlot')
  private extendedSlot: ElementRef<HTMLSlotElement>;

  constructor(protected dialog: AvlDialogComponent) {
    super(dialog);
  }

  ngAfterViewInit() {
    Promise.resolve().then(() => this.extended = !!this.extendedSlot.nativeElement.childElementCount);
  }
}
